export default {
	data() {
		return {
			list:''
		};
	},
	onLoad() {
		this._load()
	},
	methods: {
		_load() {
			this.$api.http.get('article/type_article?type=5').then(res=>{
				this.list = res.data
			})
		},
		jump(id) {
			uni.navigateTo({
				url: '/pages/article/article?id='+id,
			});
		}
	}
}
